.history {
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    width: 400px;
    background-color: #fff;

    -webkit-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    -moz-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    transition: box-shadow 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.history:hover {
    -webkit-box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
    -moz-box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
    box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
}

.history tbody tr:nth-child(odd) {
    background: #fff;
    transition: background 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.history tbody tr:nth-child(even) {
    background: #f7f7f7;
    transition: background 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.history tr:hover td {
    background: #d1f3fa;
    transition: background 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.value {
    padding: 15px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.2;
    text-align: center;
}

.headerRow {
    padding: 0px;
    margin: 0;
    background-color: rgb(131, 193, 235);
}

.tableHeader {
    padding: 10px 20px;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.1;
    color: #fff;
    text-align: center;
}