.item {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: nowrap;

    margin-left: auto;
    margin-right: auto;
    padding: 15px;
    width: 300px;
    background-color: #fff;

    -webkit-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    -moz-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    transition: box-shadow 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
    cursor: pointer;
}

.item:not(:last-child) {
    margin-bottom: 15px;
}

.item:hover {
    -webkit-box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
    -moz-box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
    box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
}

.avatar {
    display: block;
    width: 50px;
    height: 50px;
    padding: 5px;
    border-radius: 100%;
    -webkit-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    -moz-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
}

.avatar:not(:last-child) {
    margin-right: 15px;
}

.name {
    font-size: 17px;
    font-weight: 500;
    line-height: 1.1;
    color: #212121;
    text-align: center;
    cursor: pointer;
}
.status {
    display: block;
      display: block;
      width: 20px;
      height: 20px;
      margin: 20px;
      border-radius: 100%;
}
.online{
    background-color: teal;
}

.offline {
    background-color: tomato;
}