.statistics {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    margin-top: 60px;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    background-color: #fff;

    -webkit-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    -moz-box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    box-shadow: 0px 4px 5px 0px rgba(150, 150, 150, 1);
    transition: box-shadow 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
}

.statistics:hover {
    -webkit-box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
    -moz-box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
    box-shadow: 0px 0px 13px 5px rgba(122, 122, 122, 1);
}

.title {
    padding-top: 25px;
    margin: 0;
    padding-bottom: 15px;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.2;
    text-align: center;
    text-transform: uppercase;
}

.statList {
    padding: 0px;
    margin: 0;
    list-style: none;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.item {
    width: 60px;
    display: flex;
    flex-direction: column;

    padding: 12px 0px;
    cursor: pointer;
}

.label {
    padding-bottom: 10px;
    font-size: 13px;
    font-weight: 400;
    line-height: 1.1;
    color: #f5f5f5;
    text-align: center;
}

.percentage {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.1;
    color: #fff;
    text-align: center;
}